import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import Grid from '@material-ui/core/Grid';


const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    hide: {
        display: 'none'
    },
    show: {
        display: 'block'
    },
    button: {
        marginTop: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    actionsContainer: {
        marginBottom: theme.spacing(2),
    },
    resetContainer: {
        padding: theme.spacing(3),
    },
}));


export default function RiskQuestions() {
    const classes = useStyles();
    const [activeStep, setActiveStep] = React.useState(0);
    var [score, setValue] = React.useState(0);
    var [Totalscore, setTotalscore] = React.useState(0);
    const [showScore, setshowScore] = React.useState(false)

    const handleChange = (event) => {
        score += Number(event.target.value)
        setValue(score);
    };


    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const CalculateScore = () => {
        console.log(' total', score)
        let totalrisk = score / 100
        setTotalscore(totalrisk * 100)
        setshowScore(true)
    };

    return (
        <div className={classes.root}>

            <Stepper activeStep={activeStep} orientation="vertical" className={showScore ? classes.hide : classes.show}>
                <Step>
                    <StepLabel>1st Question</StepLabel>
                    <StepContent>
                        <Typography>Did you travel outside of country in the last 14 days prior to symptom onset?</Typography>
                        <div className={classes.actionsContainer}>
                            <FormControl component="fieldset">
                                <RadioGroup aria-label="question1" name="question1" onChange={handleChange}>
                                    <FormControlLabel value="0" control={<Radio />} label="Yes" />
                                    <FormControlLabel value="19" control={<Radio />} label="No" />
                                </RadioGroup>
                            </FormControl>

                            <div>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={handleNext}
                                    className={classes.button}
                                >
                                    Next
                  </Button>
                            </div>
                        </div>
                    </StepContent>
                </Step>
                <Step >
                    <StepLabel>2st Question</StepLabel>
                    <StepContent>
                        <Typography>Do you were mask when you are out?</Typography>
                        <div className={classes.actionsContainer}>
                            <FormControl component="fieldset">
                                <RadioGroup aria-label="question2" name="question2" onChange={handleChange}>
                                    <FormControlLabel value="14" control={<Radio />} label="Yes" />
                                    <FormControlLabel value="0" control={<Radio />} label="No" />
                                </RadioGroup>
                            </FormControl>
                            <div>
                                <Button
                                    disabled={activeStep === 0}
                                    onClick={handleBack}
                                    className={classes.button}
                                >
                                    Back
                  </Button>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={handleNext}
                                    className={classes.button}
                                >
                                    Next
                  </Button>
                            </div>
                        </div>
                    </StepContent>
                </Step>
                <Step >
                    <StepLabel>3st Question</StepLabel>
                    <StepContent>
                        <Typography>Do you frequently use hand sanitizer after any activity?</Typography>
                        <div className={classes.actionsContainer}>
                            <FormControl component="fieldset">
                                <RadioGroup aria-label="question3" name="question3" onChange={handleChange}>
                                    <FormControlLabel value="16" control={<Radio />} label="Yes" />
                                    <FormControlLabel value="0" control={<Radio />} label="No" />
                                </RadioGroup>
                            </FormControl>
                            <div>
                                <Button
                                    disabled={activeStep === 0}
                                    onClick={handleBack}
                                    className={classes.button}
                                >
                                    Back
                  </Button>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={handleNext}
                                    className={classes.button}
                                >
                                    Next
                  </Button>
                            </div>
                        </div>
                    </StepContent>
                </Step>
                <Step >
                    <StepLabel>4st Question</StepLabel>
                    <StepContent>
                        <Typography>If you recevie any package from online store / post do you sanitize it?</Typography>
                        <div className={classes.actionsContainer}>
                            <FormControl component="fieldset">
                                <RadioGroup aria-label="question4" name="question4"  onChange={handleChange}>
                                    <FormControlLabel value="16" control={<Radio />} label="Yes" />
                                    <FormControlLabel value="0" control={<Radio />} label="No" />
                                </RadioGroup>
                            </FormControl>
                            <div>
                                <Button
                                    disabled={activeStep === 0}
                                    onClick={handleBack}
                                    className={classes.button}
                                >
                                    Back
                  </Button>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={handleNext}
                                    className={classes.button}
                                >
                                    Next
                  </Button>
                            </div>
                        </div>
                    </StepContent>
                </Step>
                <Step >
                    <StepLabel>5st Question</StepLabel>
                    <StepContent>
                        <Typography>If you feel like having fever or headache. Do you use home remedies or visit doctor?</Typography>
                        <div className={classes.actionsContainer}>
                            <FormControl component="fieldset">
                                <RadioGroup aria-label="question5" name="question5"  onChange={handleChange}>
                                    <FormControlLabel value="15" control={<Radio />} label="Doctor" />
                                    <FormControlLabel value="0" control={<Radio />} label="Home Remidies" />
                                    <FormControlLabel value="other" control={<Radio />} label="Other" />
                            </RadioGroup>
                            </FormControl>
                            <div>
                                <Button
                                    disabled={activeStep === 0}
                                    onClick={handleBack}
                                    className={classes.button}
                                >
                                    Back
                  </Button>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={CalculateScore}
                                    className={classes.button}
                                >
                                    Finish
                  </Button>
                            </div>
                        </div>
                    </StepContent>
                </Step>
            </Stepper>

            <Grid container
                className={showScore ? classes.show : classes.hide}
                justify="space-evenly"
                alignItems="center"
            >
                <Grid className={classes.summary} item xs={12} sm={12} md={12} lg={12} xl={12}
                >
                    <Typography className={classes.summaysec} variant="h3" gutterBottom>
                        You Are 
                    </Typography>
                    <Typography className={classes.summaysec} variant="h3" gutterBottom>
                        {Totalscore + '%'} 
                    </Typography>
                    <Typography className={classes.summaysec} variant="h3" gutterBottom>
                        Safe 
                    </Typography>
                    
                </Grid>
            </Grid>
        </div>
    );
}
