import React from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import SkipNextIcon from '@material-ui/icons/SkipNext';
import Grid from '@material-ui/core/Grid';


const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    card:{
        margin: '10px 1px'
    },
    summary:{
        margin: '10px 0px 10px 0px'
    },
    summaysec:{
        marginTop:'25px',
        textAlign:'center',
        fontSize:'18px',
        fontWeight:600
    },
    cardfont:{
        fontFamily: 'archia',
        textTransform: 'uppercase'
    },
    statename:{
        color: '#073b4c',
        fontWeight: 600,
        fontFamily: 'archia',
        textTransform: 'uppercase'
    },
    stateheading:{
        color: '#9381ff',
        fontFamily: 'archia',
        textTransform: 'uppercase'
    },
    statetotal:{
        color: '#f08700',
        fontFamily: 'archia',
        textTransform: 'uppercase'
    },
    cover: {
        width: 151,
    },
    controls: {
        display: 'flex',
        alignItems: 'center',
        paddingLeft: theme.spacing(1),
        paddingBottom: theme.spacing(1),
    },
    playIcon: {
        height: 38,
        width: 38,
    },
}));

export default function MediaControlCard() {
    const classes = useStyles();
    const theme = useTheme();
    const [stateHospitalData, setstateHospitalData] = React.useState([])
    const [stateHospitalSummary, setstateHospitalSummary] = React.useState([])


    React.useEffect(() => {
        fetch('https://api.rootnet.in/covid19-in/hospitals/beds')
            .then(res => res.json())
            .then((data) => {
                setstateHospitalData(data.data.regional)
                setstateHospitalSummary(data.data.summary)
                console.log('Hospital data', data.data.summary)
            })
    }, [])

    return (
        <React.Fragment>
            <Grid container
                    justify="space-evenly"
                    alignItems="center"
                    >
                        <Grid className={classes.summary}  item xs={12} sm={12} md={12} lg={12} xl={12}
                        >
                            <Typography className={classes.summaysec} component="h6" variant="h6">
                                Summary
                            </Typography>
                        </Grid> 
                       <Grid className={classes.summary}  item xs={6} sm={4} md={1} lg={1} xl={1}
                        >
                            <Typography className={classes.statename} component="h6" variant="h6">
                                ruralHospitals
                            </Typography>
                            <Typography className={classes.statename} component="h6" variant="h6">
                                {stateHospitalSummary.ruralHospitals}
                            </Typography>
                        </Grid> 
                        <Grid className={classes.summary}  item xs={6} sm={4} md={1} lg={1} xl={1}
                        >
                            <Typography className={classes.statename} component="h6" variant="h6">
                                ruralBeds
                            </Typography>
                            <Typography className={classes.statename} component="h6" variant="h6">
                            {stateHospitalSummary.ruralBeds}

                            </Typography>
                        </Grid> 
                        <Grid className={classes.summary}  item xs={6} sm={4} md={1} lg={1} xl={1}
                        >
                            <Typography className={classes.statename} component="h6" variant="h6">
                                urbanHospitals
                            </Typography>
                            <Typography className={classes.statename} component="h6" variant="h6">
                            {stateHospitalSummary.urbanHospitals}

                            </Typography>
                        </Grid> 
                        <Grid className={classes.summary}  item xs={6} sm={4} md={1} lg={1} xl={1}
                        >
                            <Typography className={classes.statename} component="h6" variant="h6">
                                urbanBeds
                            </Typography>
                            <Typography className={classes.statename} component="h6" variant="h6">
                            {stateHospitalSummary.urbanBeds}

                            </Typography>
                        </Grid> 
                        <Grid className={classes.summary}  item xs={6} sm={4} md={1} lg={1} xl={1}
                        >
                            <Typography className={classes.statename} component="h6" variant="h6">
                                totalHospitals
                            </Typography>
                            <Typography className={classes.statename} component="h6" variant="h6">
                                {stateHospitalSummary.totalHospitals}
                            </Typography>
                        </Grid> 
                        <Grid className={classes.summary}  item xs={6} sm={4} md={1} lg={1} xl={1}
                        >
                            <Typography className={classes.statename} component="h6" variant="h6">
                                totalBeds
                            </Typography>
                            <Typography className={classes.statename} component="h6" variant="h6">
                                {stateHospitalSummary.totalBeds}
                            </Typography>
                        </Grid> 
                    </Grid>
            <Grid container
                justify="space-evenly"
                alignItems="center"
            >
                {
                    stateHospitalData.map((statedata, index) => {
                        return (
                            <Grid className={classes.card} key={index}  item xs={12} sm={12} md={3} lg={3} xl={3}
                            >
                                <Card  className={classes.root}>
                                    <div className={classes.details}>
                                        <CardContent className={classes.content}>
                                            <Grid container
                                                justify="space-evenly"
                                                alignItems="center"
                                            >
                                                <Grid item xs={12} sm={12} md={12} lg={12} xl={12}
                                                >
                                                    <Typography className={classes.statename} component="h6" variant="h6">
                                                        {statedata.state}
                                                    </Typography>
                                                </Grid>



                                                <Grid item xs={12} sm={12} md={6} lg={6} xl={6}
                                                >
                                                    <Typography className={classes.stateheading} variant="body2" component="p">
                                                        ruralHospitals
                                                        </Typography>
                                                    <Typography className={classes.statetotal} color="textSecondary" gutterBottom>
                                                        {statedata.ruralHospitals}
                                                    </Typography>
                                                </Grid>
                                                <Grid item xs={12} sm={12} md={4} lg={4} xl={4}
                                                >
                                                    <Typography className={classes.stateheading} variant="body2" component="p">
                                                        ruralBeds
                                                        </Typography>
                                                    <Typography  className={classes.statetotal} color="textSecondary" gutterBottom>
                                                        {statedata.ruralBeds}
                                                    </Typography>
                                                </Grid>
                                                <Grid item xs={12} sm={12} md={6} lg={6} xl={6}
                                                >
                                                    <Typography className={classes.stateheading} variant="body2" component="p">
                                                        totalHospitals
                                                        </Typography>
                                                    <Typography className={classes.statetotal} color="textSecondary" gutterBottom>
                                                        {statedata.totalHospitals}
                                                    </Typography>
                                                </Grid>
                                                <Grid item xs={12} sm={12} md={4} lg={4} xl={4}
                                                >
                                                    <Typography className={classes.stateheading} variant="body2" component="p">
                                                        totalBeds
                                                        </Typography>
                                                    <Typography className={classes.statetotal} color="textSecondary" gutterBottom>
                                                        {statedata.totalBeds}
                                                    </Typography>
                                                </Grid>
                                                <Grid item xs={12} sm={12} md={6} lg={6} xl={6}
                                                >
                                                    <Typography className={classes.stateheading} variant="body2" component="p">
                                                        urbanHospitals
                                                        </Typography>
                                                    <Typography className={classes.statetotal} color="textSecondary" gutterBottom>
                                                        {statedata.urbanHospitals}
                                                    </Typography>
                                                </Grid>
                                                <Grid item xs={12} sm={12} md={4} lg={4} xl={4}
                                                >
                                                    <Typography className={classes.stateheading} variant="body2" component="p">
                                                        urbanBeds
                                                        </Typography>
                                                    <Typography className={classes.statetotal} color="textSecondary" gutterBottom>
                                                        {statedata.urbanBeds}
                                                    </Typography>
                                                </Grid>
                                            </Grid>
                                        </CardContent>
                                    </div>
                                </Card>
                            </Grid>
                        )
                    })
                }
            </Grid>

        </React.Fragment>
    );
}
