import React from 'react';
import './aboutus.css';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';


const About = () => {
    return (
        <Grid container
            justify="space-evenly"
            alignItems="center"
        >
            <Grid item xs={12} sm={12} md={12} lg={12} xl={12}
            >
                <div className="header">
                    <h1 className="page-heading">
                        <span className="page-heading-primary">I Build Apps That help humanity </span>
                        <span className="page-heading-secondary">#StrongTogather</span>
                    </h1>
                </div>
            </Grid>
            <Grid className='secondsec' item xs={12} sm={12} md={12} lg={12} xl={12}
            >
                <Typography  variant="h4" gutterBottom>
                Hello people! I'm Ajay
                </Typography>
                <Typography  variant="h5" gutterBottom>
                I use my development skills to make some live easier.
                </Typography>
                <Typography  variant="h5" gutterBottom>
                Building softwares that inspire people.
                </Typography>
            </Grid>
            <Grid className='secondsec' item xs={12} sm={12} md={6} lg={6} xl={6}
            >
                <Typography  variant="h4" gutterBottom>
                About Me
                </Typography>
                <Typography  variant="h5" gutterBottom>
                I am a front web app developer gaining experience and skill by building small but usefull application that might help someone someday also starting app development using react native.
                </Typography>
            </Grid>
        </Grid>

    )

}

export default About;
